* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;

}

.nav-list {
    height: 10vh;
    display: flex;
    width: 50%;
    justify-content: space-around;
    align-items: center;
    margin-left: auto;
    list-style: none;
    z-index: 99;
}

.nav-link {
    text-decoration: none;
    color: #131313;
    font-size: 18px;

}


.home, 
.about,
.contact {
    height: 90vh;
    position: absolute;
    width: 100%;
}

.home-content, 
.about-content,
.contact-content {
    display: flex;
    height: 100%;
    justify-content: space-around;
    align-items: center;

    img {
        height: 50%;
    }

    h1 {
        font-size: 50px;
        margin: 30px 0px;
    }
    p{
        font-size: 20px;
    }

}

.home {
    background-image: linear-gradient(45deg, #ff9a9e 0%, #fad0c4 99%, #fad0c4 100%);
}

.about {
    background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
}

.contact {
    background-image: linear-gradient(to top, #fad0c4 0%, #ffd1ff 100%);
}

